<template>
  <div class="frame">
    <div class="logIn">
      <div class="title">
        <span class="mento">MENTO</span>
        <span class="find">FIND YOUR MENTOR</span>
      </div>
      <div class="btn">
        <button>로그인</button>
      </div>
    </div>
    <div class="signUp">
      <div class="title">회원가입</div>
      <form action="#" class="signUp">
        <div class="radio">
          <input ref="rd1" type="radio" name="kind" id="radio1"><label for="radio1" required>멘티로 가입</label>
          <input ref="rd2" type="radio" name="kind" id="radio2"><label for="radio2" required>멘토로 가입</label>
          <div v-if="alert == 5" class="alert"><span><i class="fas fa-exclamation"></i>&nbsp;&nbsp;둘 중 하나를 선택해주세요.</span></div>
        </div>
        <div class="text">
          <input
            v-model="name"
            ref="name"
            type="text"
            name="name"
            placeholder="이름(실명)을 입력해주세요"
            minlength="2"
            maxlength="6"
            required
          />
          <div v-if="alert == 1" class="alert"><span><i class="fas fa-exclamation"></i>&nbsp;&nbsp;유효하지 않은 이름 입니다.</span></div>
        </div>
        <div class="text">
          <input
            v-model="email"
            type="email"
            name="email"
            placeholder="이메일(example@mento.com)을 입력해주세요"
            ref="name"
            required
          />
          <div v-if="alert == 2" class="alert"><span><i class="fas fa-exclamation"></i>&nbsp;&nbsp;양식에 맞지 않는 이메일 입니다.</span></div>
        </div>
        <div class="text">
          <input
            v-model="pw"
            type="password"
            name="password"
            placeholder="비밀번호(영문+숫자 조합 8~16자리) 입력해주세요"
            minlength="8"
            maxlength="16"
            required
          />
          <div v-if="alert == 3" class="alert"><span><i class="fas fa-exclamation"></i>&nbsp;&nbsp;양식에 맞지 않는 비밀번호입니다.</span></div>
        </div>
        <div class="text">
          <input
            v-model="repw"
            type="password"
            name="password"
            placeholder="비밀번호를 다시 입력해주세요"
            minlength="8"
            maxlength="16"
            required
          />
          <div v-if="alert == 4" class="alert"><span><i class="fas fa-exclamation"></i>&nbsp;&nbsp;비밀번호가 일치하지 않습니다.</span></div>
        </div>
        <div class="checkbox">
          <div class="cb">
            <input ref="cb" type="checkbox" name="cb1" id="cb1" required/><label for="cb1"
              >이용약관, 개인정보 수집 및 이용 동의 (필수)</label
            >
          </div>
          <div class="cb">
            <input ref="cb" type="checkbox" name="cb2" id="cb2" required/><label for="cb2">만 14세 이상 (필수)</label>
          </div>
        </div>
        <div class="btn">
          <input type="submit" value="회원가입" @click="next">
        </div>
      </form>
    </div>
  </div>
</template>

<script>
export default {
  name: "SignUp",
  data() {
    return{
        name: null,
        email: null,
        pw: null,
        repw: null,
        alert: 0,
    }
  },
  computed: { 
    nameValid () { 
      return /^[가-힣]{2,6}$/.test(this.name)
      }, 
    emailValid () { 
      return /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/i.test(this.email)
      },
    pwValid () { 
      return /^(?=.*\d)(?=.*[a-zA-Z])[0-9a-zA-Z]{8,16}$/.test(this.pw)
      } 
    },
  methods: {
    next(){
      if(!this.nameValid){
        return this.alert = 1;
      } else if(!this.emailValid){
        return this.alert = 2;
      } else if(!this.pwValid){
        return this.alert = 3;
      } else if(this.pw != this.repw){
        return this.alert = 4;
      }else if(!this.$refs.rd1.checked && !this.$refs.rd2.checked){
        return this.alert = 5;
      } else if(this.$refs.cb.checked && (this.$refs.rd1.checked || this.$refs.rd2.checked)){
        alert('회원가입이 완료되었습니다');
        return true;
      }
    }
    }
};
</script>

<style>
.frame {
  display: flex;
  height: 600px;
  width: 1000px;
  margin-top: 50px;
  border-right: 1px solid #e7e2df;
}
.frame .logIn {
  background-color: #3e3e3e;
  box-shadow: inset 0px 0px 5px #E7E2DF;
  /* opacity: 0.8; */
  width: 45%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.frame .logIn .title {
  text-align: center;
  /* background-color:#dbdbdb62; */
}
.frame .logIn .title span {
  display: block;
}
.frame .logIn .title .mento {
  color: #ffc245;
  font-size: 50px;
  font-weight: bold;
}
.frame .logIn .title .find {
  color: #e7e2df;
  font-size: 35px;
  font-weight: bold;
}
.frame .logIn .btn {
  width: 100%;
  margin-top: 100px;
}
.frame .logIn .btn button {
  border: none;
  border-radius: 40px;
  width: 200px;
  height: 40px;
  font-size: 15px;
  background: #fff;
}
.frame .signUp {
  background-color: #ffffff;
  width: 55%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.frame .signUp .title{
  font-size: 25px;
  width: 100%;
  text-align: center;
}
.frame .signUp form{
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.frame .signUp form .radio{
  margin: 30px 0 10px 0;
  width: 100%;
  display: flex;
  justify-content: center
}
.frame .signUp form .radio input{
  margin-right: 5px;
	width: 20px;
	height: 20px;
	border: 2px solid #3E3E3E;
	cursor: pointer;
}
.frame .signUp form .radio label{
  font-size: 13px;
  font-weight: bold;
  margin-right: 15px;
}
.frame .signUp form .text, .frame .signUp form .radio{
  position: relative;
}
.frame .signUp form .text input {
  border: 1px solid #3e3e3e;
  box-shadow: 0px 0px 40px #00000029;
  border-radius: 40px;
  width: 400px;
  height: 40px;
  font-size: 15px;
  margin-top: 20px;
  padding: 20px;
}
.frame .signUp form input:focus {
  outline: none;
}
.frame .signUp form input::placeholder {
  color: #E7E2DF;
}
.frame .signUp form .radio .alert{
  margin: 0 50px;
  background: #ffffff;
  color: #3e3e3e;
  box-shadow: 0px 0px 20px #00000029;
  position: absolute;
  top: 30px;
  height: 40px;
  font-size: 15px;
  z-index: 1;
  display: flex;
  align-items: center;
}
.frame .signUp form .text .alert{
  margin: 0 50px;
  background: #ffffff;
  color: #3e3e3e;
  box-shadow: 0px 0px 20px #00000029;
  position: absolute;
  top: 65px;
  height: 40px;
  font-size: 15px;
  z-index: 1;
  display: flex;
  align-items: center;
}
.frame .signUp form .text .alert i, .frame .signUp form .radio .alert i{
  color: red;
}
.frame .signUp form .checkbox{
  margin: 40px 0 20px 0;
}
.frame .signUp form .checkbox .cb{
  display: flex;
}
.frame .signUp form .checkbox input{
	width: 20px;
	height: 20px;
	border: 2px solid #3E3E3E;
	cursor: pointer;
  margin-right: 5px;
}
.frame .signUp form .checkbox label{
  font-size: 13px;
}
.frame .signUp .btn input {
  border: none;
  border-radius: 40px;
  width: 400px;
  height: 40px;
  font-size: 15px;
  background: #fdc344;
  box-shadow: 0px 0px 40px #00000029;
}
</style>
