<template>
  <div class="knowhow">
    <div class="title">{{ ContentData.title }}</div>
    <div class="btnWrap">
      <div class="contentsWrap">
        <div class="contents" ref="contents">
          <div class="content" v-for="(a, i) in ContentData.content" :key="i">
            <a :href="ContentData.content[i].href">
              <img
                class="content_img"
                :src="ContentData.content[i].img"
                alt=""
              />
              <span class="content_title">{{
                ContentData.content[i].title
              }}</span>
              <span class="content_name">{{
                ContentData.content[i].name
              }} 멘토</span>
            </a>
          </div>
        </div>
      </div>
      <button class="before" @click="before">
        <i class="fas fa-angle-left"></i>
      </button>
      <button class="next" @click="next">
        <i class="fas fa-angle-right"></i>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: "Knowhow",
  props: {
    ContentData: Array,
  },
  data() {
    return {
      nowPicture: 1,
    };
  },
  methods: {
    next() {
      if (this.nowPicture == 1) {
        this.$refs.contents.style.transition = "transform 1s";
        this.$refs.contents.style.transform = "translateX(-615px)";
        return (this.nowPicture = 4);
      } else if (this.nowPicture == 4) {
        this.$refs.contents.style.transition = "transform 1s";
        this.$refs.contents.style.transform = "translateX(-1230px)";
        return (this.nowPicture = 7);
      }
    },
    before() {
      if (this.nowPicture == 7) {
        this.$refs.contents.style.transition = "transform 1s";
        this.$refs.contents.style.transform = "translateX(-615px)";
        return (this.nowPicture = 4);
      } else if (this.nowPicture == 4) {
        this.$refs.contents.style.transition = "transform 1s";
        this.$refs.contents.style.transform = "translateX(0px)";
        return (this.nowPicture = 1);
      }
    },
  },
};
</script>

<style>
.knowhow {
  background-color: #f5f0ec;
  margin: 0 auto;
  padding-top: 100px;
  /* padding-bottom: 100px; */
}
.knowhow .title {
  width: 650px;
  margin: 0 auto;
  font-size: 25px;
  font-weight: bold;
}
.knowhow .btnWrap{
  position: relative;
  width: 700px;
  margin: 0 auto;
}
.knowhow .contentsWrap {
  width: 650px;
  margin: 0 auto;
  overflow: hidden;
}
.knowhow .contents {
  margin: 0 auto;
  width: 650px;
  /* background: rgba(255, 201, 201, 0.425); */
  display: flex;
  /* box-shadow: 0px 0px 40px #00000029; */
}
.knowhow .content {
  margin-top: 20px;
  margin-right: 5px;
  text-align: center;
  
  /* background-color: rgba(117, 196, 199, 0.514); */
  /* background: rgb(221, 202, 202); */
  /* float: left; */
}
/* .knowhow .content:hover{
  transition: 0.5s all ease;
  font-size: 1.1em;
} */
/* .knowhow .content:hover{
  transition: 0.5s all ease;
  padding-bottom: 10px;
} */
.knowhow .content .content_img {
  display: block;
  margin-bottom: 5px;
  border-radius: 10px;
  width: 200px;
  height: 150px;
  transition: 0.5s all ease;
  /* box-shadow: 0px 0px 40px #00000029; */
}
.knowhow .content .content_img:hover{
  width: 220px;
  height: 165px;
}
.knowhow .content .content_title {
  font-weight: bold;
}
.knowhow .content .content_name {
  font-size: 13px;
}
.knowhow .content span{
  display: block;
}
.knowhow button.before {
  position: absolute;
  top: 40%;
  /* left: 80px; */
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: none;
  background: #ffffff;
  box-shadow: 0px 0px 40px #00000029;
}
.knowhow button.next {
  position: absolute;
  top: 40%;
  right: 0px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: none;
  background: #ffffff;
  box-shadow: 0px 0px 40px #00000029;
}
.knowhow .content a {
  text-decoration: none;
  color: inherit;
}
.knowhow .content a:hover {
  text-decoration: none;
  color: inherit;
}
</style>
